import { dataConst } from './defaultData';

export default (editor, opts = {}) => {
    const { id, label, classPrefix } = opts;

    const idIndicators = `${id}-indicators`;
    const idSlideshow = `${id}-inner`;
    const idControlPrev = `${id}-control-prev`;
    const idControlNext = `${id}-control-next`;

    editor.DomComponents.addType(id, {
        model: {
            defaults: {
                droppable: false,
                name: label,
                attributes: {
                    id: `${id}`,
                    class: `${classPrefix} slide`,
                    "data-ride": "carousel",
                },
                components: [
                    { type: idIndicators },
                    { type: idSlideshow },
                    { type: idControlPrev },
                    { type: idControlNext },
                ],
                styles: (opts.style || ``) + `.${classPrefix}-inner{height: inherit;}.${classPrefix}-inner img {height: inherit; object-fit: cover;}`,
                script: function () {
                    $(document).ready(function () {
                        $(this).find('.carousel-item:first').addClass("active");
                        $(this).carousel();
                    })
                },
                _slide: 3,
                slide: 3,
                traits: [
                    {
                        type: 'number',
                        name: 'slide', // Required and available for all traits
                        label: 'Slide', // The label you will see near the input
                        placeholder: '1-10',
                        min: 1, // Minimum number value
                        max: 10, // Maximum number value
                        step: 1, // Number of steps
                        changeProp: true,
                    }
                ],
                'script-props': ['slide'],
            },

            init() {
                this.on('change:slide', this.handleSlideTypeChange);
            },

            handleSlide__Indicators(to_number){
                const currentNumSlides = this.components().models[0].components().length;
                if (to_number > currentNumSlides){
                    [...Array(to_number - currentNumSlides).keys()].map(
                        (_key) => {
                            this.components().models[0].components().add({type: idIndicatorsItem});
                        }
                    )
                } else if (to_number < currentNumSlides){
                    [...Array(currentNumSlides - to_number).keys()].map(
                        (_key) => {
                            this.components().models[0].components().pop();
                        }
                    )
                }
            },

            handleSlide__Inner(to_number){
                const currentNumSlides = this.components().models[1].components().length;
                if (to_number > currentNumSlides){
                    [...Array(to_number - currentNumSlides).keys()].map(
                        (_key) => {
                            this.components().models[1].components().add({type: SlideshowItem});
                        }
                    )
                } else if (to_number < currentNumSlides){
                    [...Array(currentNumSlides - to_number).keys()].map(
                        (_key) => {
                            this.components().models[1].components().pop();
                        }
                    )
                }
            },

            handleSlideTypeChange() {
                const numSlides = Number.parseInt(this.get('slide')) || this.get('_slide');

                this.handleSlide__Indicators(numSlides);
                this.handleSlide__Inner(numSlides);
            },
        },
    });

    // Indicators
    const idIndicatorsItem = `${id}-indicators-item`

    editor.DomComponents.addType(idIndicators, {
        model: {
            defaults: {
                name: `${label}-indicators`,
                tagName: 'ul',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    class: `${classPrefix}-indicators`,
                },
                components: [
                    {
                        type: idIndicatorsItem,
                        attributes: {
                            "data-slide-to": 0,
                            "class": "active",
                        }
                    },
                    {
                        type: idIndicatorsItem,
                        attributes: {
                            "data-slide-to": 1,
                        }
                    },
                    {
                        type: idIndicatorsItem,
                        attributes: {
                            "data-slide-to": 2,
                        }
                    },
                ],
            }
        }
    });

    editor.DomComponents.addType(idIndicatorsItem, {
        model: {
            defaults: {
                name: `${label}-indicators-item`,
                tagName: 'li',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    "data-target": `#${id}`,
                    "data-slide-to": "0",
                },
            }
        }
    })

    // -- Indicators

    // Slideshow
    const SlideshowItem = `${id}-inner-item`

    editor.DomComponents.addType(idSlideshow, {
        model: {
            defaults: {
                name: `${label}-inner`,
                tagName: 'div',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    class: `${classPrefix}-inner`,
                },
                components: [
                    { type: SlideshowItem },
                    { type: SlideshowItem },
                    { type: SlideshowItem },
                ],
            },
        }
    });

    editor.DomComponents.addType(SlideshowItem, {
        model: {
            defaults: {
                name: `${label}-inner-item`,
                tagName: 'div',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    class: `${classPrefix}-item`,
                },
                components: [
                    {
                        name: `${label}-slideshow`,
                        type: "image",
                        traits: [
                            {
                                type: 'button',
                                label: 'src',
                                name: 'src',
                                text: 'Change Image',
                                full: true,
                                command: (editor) => {
                                    editor.AssetManager.open({
                                        types: ['image'],
                                        select(asset, complete) {
                                            window.xxx_asset = asset;

                                            const selected = editor.getSelected();
                                            if (selected && selected.is('image')) {
                                                selected.addAttributes({ src: asset.getSrc() });
                                                selected.getTrait('src').set(asset.getSrc());
                                                complete && editor.AssetManager.close();
                                            }
                                        }
                                    });
                                },

                            },
                            'name', // Same as: { type: 'text', name: 'name' }
                            'placeholder',
                        ],
                        attributes: {
                            class: "d-block w-100",
                            src: dataConst.srcImage,
                        },
                    },
                    {
                        name: `${label}-captions`,
                        tagName: 'div',
                        droppable: false,
                        editable: false,
                        highlightable: false,
                        attributes: {
                            class: `${classPrefix}-caption d-none d-md-block`,
                        },
                        components: [
                            {
                                name: `${label}-captions-label`,
                                type: 'text',
                                droppable: false,
                                editable: true,
                                tagName: 'h5',
                                components: 'Slide label',
                            },
                            {
                                name: `${label}-captions-remark`,
                                type: 'text',
                                droppable: false,
                                editable: true,
                                tagName: 'p',
                                components: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
                            }
                        ]
                    },
                ],
            }
        }
    })
    // -- Slideshow

    // Control Prev
    const idIconControlPrev = `${id}-innher-control-icon-prev`;

    editor.DomComponents.addType(idControlPrev, {
        model: {
            defaults: {
                tagName: 'a',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    "class": `${classPrefix}-control-prev`,
                    "href": `#${id}`,
                    "data-slide": "prev",
                },
                components: [
                    { type: idIconControlPrev },
                ]
            }
        }
    });

    editor.DomComponents.addType(idIconControlPrev, {
        model: {
            defaults: {
                tagName: 'span',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    class: `${classPrefix}-control-prev-icon`,
                }
            }
        }
    })
    // -- Control Prev

    // Control Next
    const idIconControlNext = `${id}-innher-control-icon-next`;
    editor.DomComponents.addType(idControlNext, {
        model: {
            defaults: {
                tagName: 'a',
                droppable: false,
                draggable: false,
                highlightable: false,
                attributes: {
                    "class": `${classPrefix}-control-next`,
                    "href": `#${id}`,
                    "data-slide": "next",
                },
                components: [
                    { type: idIconControlNext },
                ]
            }
        }
    });

    editor.DomComponents.addType(idIconControlNext, {
        model: {
            defaults: {
                tagName: 'span',
                attributes: {
                    class: `${classPrefix}-control-next-icon`,
                }
            }
        }
    })
    // -- Control Next
};
